:root {
    --maincolor:   #2A606C;
    --subcolor:   #66B0C1;
    --grey: #BABABA;
}
.bodystyle{
    background-color: #E5E5E5;
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    margin: 0;
    font-family: "Segoe UI";
    color: white;
}

.main-header {
    background-color: #2A606C;
    background-color: var(--maincolor);
    margin-bottom: 2.5em; 
    height: 84px;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: space-between;   
}

.modal-header{
    background-color: #2A606C;
    background-color: var(--maincolor);
    font-size: 20px;
    text-align: center;
    height: 60px;
}
.modal-header .close {
    margin-top: -45px;
    margin-right: -10px;
}

.main-body {
    margin-bottom: 2.5em;
    display: flex;
    justify-content: center;  
    width: 100%;
    flex: 1 1;
}

.main-footer {
    background-color: #66B0C1;
    background-color: var(--subcolor);
    height: 65px;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: space-between;   
}
.modual-footer{
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: space-between; 
}

.sub-header1 {
    display: flex;
    align-items: center;
    flex-direction:row;
    width: 100%;
    justify-content: space-around;  
    max-width: 550px
}

.sub-header2 {
    display: flex;
    align-items: center;
    flex-direction:row;
    width: 100%;
    justify-content: space-around;  
    max-width: 400px
}

.sub-body {
    width: 80%; 
    max-width: 70%;
    min-width: 100px;
}

.sub-body1 {
    display: flex;
    flex-direction:row;
    justify-content: space-between; 
    vertical-align: bottom;
}

.sub-body2 {
    display: flex;
    flex-direction:row;
    justify-content: space-between; 
}

.h-1 {
    font-size: 14px;
    line-height: 1.7;
    text-align: center;
    padding-left: 10px
}

.h-2 {
    width:70px;
}

.h-3 {
    font-weight: bold;
    font-size: 38px;
    line-height: 1.1;
}

.h-4 {
    font-size: 12px;
    line-height: 2;
}

.h-5 {
    font-weight: bold;
    font-size: 10px;
    padding-right: 10px
}

.b-1 {
    color: #BABABA;
    color: var(--grey); 
    text-align: left;
    font-size: 12px;
    padding-top: 7px
}

.b-2 {
    color: #66B0C1;
    color: var(--subcolor); 
    text-align: right;
}

.b-4 {
    margin-top: -7px;
    color: #66B0C1;
    color: var(--subcolor); 
    text-align: left;
    font-size: 18px;
    text-decoration: underline;
}

.b-5 {
    margin-top:   -5px;
    color: #BABABA;
    color: var(--grey);  
    text-align: right;
    font-size: 12px;
}

.b-3 {
    text-align: right;
    margin-top: -5px;
}

.b-6 {
    text-align: left;
    width: 50%;
}

b-7{
    text-align: right;
    width: 50%;

}

input[type=text], select {
    width: 80%;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

textarea[type=text2], select { 
    color: black;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    height: 150px;
    font-size:10pt;
    width: 100%
}

input[type=submit] {
    font-size: 12px;
    width: 100%;
    background-color: #66B0C1;
    background-color: var(--subcolor);
    color: white;
    padding: 10px 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: 550;
}

input[type=submit2] {
    font-size: 12px;
    width: 60px;
    background-color: #66B0C1;
    background-color: var(--subcolor);
    color: white;
    padding: 10px 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: 550;
    text-align: center;
}

input[type=link] {
    font-size: 12px;
    width: 100px;
    background-color: #2A606C;
    background-color: var(--maincolor);
    color: white;
    padding: 10px 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: 550;
    text-align: center;
}

.circlebutton{
    background-color: white;
    border: none;
    color:  #BABABA;
    color:  var(--grey); 
    text-align: center;
    font-size: 20px;
    border-radius: 100%;
    width: 25px;
    height: 25px;
    line-height: 10px;
    padding-bottom: 5px;
    font-family: "Segoe UI";
    margin-left: 20px;
    margin-right: 20px;
}

.circlebutton:hover{
    box-shadow: 2px 3px 7px 0 rgba(0,0,0,0.24), 0 3px 10px 0 rgba(0,0,0,0.19);
    -webkit-transition: box-shadow 0.3s ease-in-out;
    transition: box-shadow 0.3s ease-in-out;
    cursor: pointer;
}

.centeronebutton{
    display: flex;
    justify-content: center;
    flex-direction: row;
    margin-top: -18px;
}

.notbutton{
    font-size: 18px;
    text-decoration: underline;
    border: none;
    background-color: inherit;
    font-size: 16px;
    cursor: pointer;
    display: inline-block;
    color: #66B0C1;
    color: var(--subcolor);
}
.close{
    color:white;
    opacity: .8;
    font-weight: 100;
}
.close:hover{
    color:white;
}
a.button {
    -webkit-appearance: button;
    -moz-appearance: button;
    appearance: button;

    text-decoration: none;
    color: #000;
    color: initial;
}
